<template>
    <div>
        <el-row class="users-header" type="flex" justify="space-between">
            <el-button type="primary" size="mini" @click="createUser">新建</el-button>
            <el-col>
                <el-input placeholder="请输入用户" clearable class="user-serach" v-model="search"></el-input>
            </el-col>
            <el-button type="danger" size="mini" @click="undoHistory" :disabled="undoDisabled">撤销</el-button>
        </el-row>

        <el-table class="user-table" :data="userData" border style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180" header-align="center" align="center">
            </el-table-column>
            <el-table-column prop="age" label="年龄" width="180" header-align="center" align="center">
            </el-table-column>
            <el-table-column prop="sex" label="性别" width="180" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.sex == 0 ? '女' : '男' }}</span>
                </template>
            </el-table-column>
            <el-table-column prop="tel" label="联系电话" width="180" header-align="center" align="center">
            </el-table-column>
            <el-table-column prop="DetailedAddress" label="地址" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row | addrFormat() }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button type="danger" size="mini" @click="deleteChecked">批量删除</el-button>

    </div>
</template>

<script>
export default {
    props: {
        tableData: {
            type: Array,
            default: []
        },
        historyData: {
            type: Object,
            default: {
                index: -1,
                maxStep: 5,
                steps: [],
            }
        }
    },
    data() {
        return {
            search: '',
            multipleSelection: []
        }
    },

    computed: {
        undoDisabled() {
            return !((this.historyData.index > 0) && (this.historyData.steps.length > 0))
        },
        userData() {
            if (!!this.search) {
                var search = this.search
                return this.tableData.filter(function (item) {
                    return item.name.indexOf(search) != -1
                })
            } else {
                return this.tableData
            }
        }
    },
    methods: {
        handleEdit(index, row) {
            this.$bus.$emit('showDialog', row, index)
        },
        handleDelete(index) {
            this.$emit('delUser', index)
        },
        createUser() {
            this.$bus.$emit('showDialog')
        },
        handleSelectionChange(val) {
            this.multipleSelection = val.map(item => {
                return item.userId
            })
        },
        deleteChecked() {
            this.$emit('delChecked', this.multipleSelection)
            // var data = this.tableData;
            this.multipleSelection = []
        },
        undoHistory() {
            this.$emit('undoHistoryStep')
        }
    },
    filters: {
        addrFormat(row) {
            if (!!row.DetailedAddress) {
                return row.address.join('/') + '/' + row.DetailedAddress
            } else {
                return row.address.join('/')
            }
        }
    }
}
</script>

<style lang="less" scoped>
.user-table {
    margin: 20px 0;
}

.user-serach {
    width: 200px;
    padding-left: 15px;
}
</style>