<template>
    <div>
        <!-- 弹出框 -->
        <el-dialog title="新建/编辑用户" :visible.sync="showEdit" class="user-form" center>
            <el-form :model="userInfo" label-position="top" ref="userForm" :rules="rules">
                <el-row :gutter="24" type="flex" justify="center">
                    <el-col :span="10">
                        <el-form-item label="姓名" prop="name">
                            <el-input v-model="userInfo.name" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="10">
                        <el-form-item label="性别">
                            <el-select v-model="userInfo.sex" placeholder="请选择" class="size-full">
                                <el-option label="女" value="0"></el-option>
                                <el-option label="男" value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24" type="flex" justify="center">
                    <el-col :span="10">
                        <el-form-item label="联系电话">
                            <el-input v-model="userInfo.tel" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="10">
                    </el-col>
                </el-row>

                <el-row :gutter="24" type="flex" justify="center">
                    <el-col :span="8">
                        <el-form-item label="详细地址" prop="address">
                            <el-cascader v-model="userInfo.address" :options="options" class="size-full"
                                :props="{ expandTrigger: 'hover' }"></el-cascader>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=" ">
                            <el-input v-model="userInfo.DetailedAddress" autocomplete="off" placeholder="请输入详细地址">
                            </el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="onSubmit">{{ this.type === 'add' ? '添加' : '保存' }}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            showEdit: false,
            options: [{
                value: '广东省',
                label: '广东省',
                children: [{
                    value: '广州市',
                    label: '广州市',
                    children: [{
                        value: '荔湾区',
                        label: '荔湾区'
                    }, {
                        value: '越秀区',
                        label: '越秀区'
                    }, {
                        value: '海珠区',
                        label: '海珠区'
                    }, {
                        value: '天河区',
                        label: '天河区'
                    }]
                }]
            }],
            userInfo: {
                userId: '',
                name: '',
                date: null,
                sex: null,
                tel: '',
                address: [],
                DetailedAddress: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                    { min: 2, max: 10, message: '名称长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                address: [
                    { required: true, message: '地址不能为空' },
                ]
            },
            type: '',
            userIndex: null
        };
    },

    mounted() {
        this.$bus.$on('showDialog', (info, index) => {
            this.userInfo = {
                userId: '',
                name: '',
                date: null,
                sex: null,//sex: null,
                tel: '',
                address: [],
                DetailedAddress: ''
            }
            if (!!info) {
                this.type = 'edit'
                this.userInfo = { ...info }
                this.userIndex = index
            } else {
                this.type = 'add'
                this.userIndex = null
            }
            this.showEdit = true
        })
    },
    beforeDestroy() {
        // this.$bus.off()
    },
    methods: {
        onSubmit() {
            this.$refs.userForm.validate((valid) => {
                if (valid) {
                    this.$emit('addUser', this.userInfo, this.userIndex)
                    this.userInfo = {
                        userId: '',
                        name: '',
                        date: null,
                        sex: null,//sex: null,
                        tel: '',
                        address: [],
                        DetailedAddress: ''
                    }
                    this.showEdit = false
                    this.userIndex = -1
                } else {
                    return false;
                }
            })

        },
        cancel() {
            // this.$refs['userForm'].resetFields();
            this.userInfo = {
                name: '',
                date: null,
                sex: null,//sex: null,
                tel: '',
                address: [],
                DetailedAddress: '',
                type: ''
            }
            this.showEdit = false
        }
    },
};
</script>

<style lang="less" scoped>
.user-form {

    ::v-deep .el-form--label-top .el-form-item__label {
        padding: 0px;
        height: 35px
    }
}

.el-row {
    height: 90px!important;
}

// .el-scrollbar__wrap {
//     height: 0%!important;
// }

</style>