<template>
  <div id="app">
    <div class="users-title">用户管理</div>
    <el-card class="user-card">
      <Users :table-data="tableData" @delUser="delUser" @delChecked="delChecked" :history-data="historyData"
        @undoHistoryStep="undoHistoryStep"></Users>
      <UserDialog @addUser="addUser(arguments)"></UserDialog>
    </el-card>
  </div>
</template>

<script>
import Users from '@/views/users'
import UserDialog from '@/components/userDialog'
import { generateId, deepClone } from "@/utils/util"

import { Message } from "element-ui";

import userData from '@/userData'
export default {
  name: 'App',
  components: {
    Users,
    UserDialog
  },
  data() {
    return {
      tableData: [],
      historyData: {
        index: -1,  //index: 0,
        maxStep: 5,
        steps: [],
      }
    }
  },
  methods: {
    addUser(param) {
      if (param[1] != null) {
        this.tableData.splice(param[1], 1, deepClone(param[0]))
        this.emitHistoryChange()
        Message({
          type: "success",
          message: "修改成功!"
        })
      } else {
        param[0].userId = generateId()
        this.tableData.push(deepClone(param[0]))
        this.emitHistoryChange()
        Message({
          type: "success",
          message: "保存成功!"
        })
      }
      sessionStorage.setItem('tableData', JSON.stringify(this.tableData))
    },
    delUser(userIndex) {
      if (userIndex >= 0) {
        this.tableData.splice(userIndex, 1)
        this.emitHistoryChange()
        sessionStorage.setItem('tableData', JSON.stringify(this.tableData))
        Message({
          type: "success",
          message: "删除成功!"
        })
      }
    },
    delChecked(userArray) {
      this.tableData = this.tableData.filter(item => {
        return userArray.indexOf(item.userId) == -1
      })
      sessionStorage.setItem('tableData', JSON.stringify(this.tableData))
      this.emitHistoryChange()
      Message({
        type: "success",
        message: "删除成功!"
      })
    },
    emitHistoryChange() {
      // var test = this.tableData
      // if (this.historyData <= 3) {
      //   this.historyData.push(test)
      // } else {
      //   this.historyData.splice(0, 1)
      //   this.historyData.push(this.tableData)
      // }
      if (this.historyData.index === this.historyData.maxStep - 1) {
        this.historyData.steps.shift()
      } else {
        this.historyData.index++
      }
      this.historyData.steps[this.historyData.index] = _.cloneDeep(this.tableData)

      if (this.historyData.index < this.historyData.steps.length - 1) {
        this.historyData.steps = this.historyData.steps.slice(0, this.historyData.index + 1)
      }
    },
    undoHistoryStep() {
      if (this.historyData.index !== 0) {
        this.historyData.index--
      }
      this.tableData = deepClone(this.historyData.steps[this.historyData.index])
    },
  },
  mounted() {
    sessionStorage.getItem('tableData') ? (this.tableData = JSON.parse(sessionStorage.getItem('tableData'))) : this.tableData = userData
    this.emitHistoryChange()
  }
}
</script>

<style lang="less" scoped>
.users-title {
  height: 80px;
  background-color: #ececec;
  text-align: center;
  line-height: 80px;
  font-size: 20px;
  font-weight: 800;
}

.user-card {
  margin: 20px auto;
  width: 90%;
}
</style>
